"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Badge } from "@/components/ui/badge"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { FaSmile, FaLaughBeam, FaGrinStars, FaSadTear } from "react-icons/fa"

const allPlans = [
  { id: "p1", cpu: 2, ram: 2, ramType: "DDR5", storage: 35, storageType: "NVMe", price: 6, pricePerHour: 0.02, name: "2 Cores | 2 GB DDR5 | 35 GB", popular: false },
  { id: "p2", cpu: 2, ram: 4, ramType: "DDR5", storage: 55, storageType: "NVMe", price: 10, pricePerHour: 0.03, name: "2 Cores | 4 GB DDR5 | 55 GB", popular: false },
  { id: "p3", cpu: 4, ram: 6, ramType: "DDR5", storage: 85, storageType: "NVMe", price: 18, pricePerHour: 0.04, name: "4 Cores | 6 GB DDR5 | 85 GB", popular: true },
  { id: "p4", cpu: 4, ram: 8, ramType: "DDR5", storage: 115, storageType: "NVMe", price: 23, pricePerHour: 0.05, name: "4 Cores | 8 GB DDR5 | 115 GB", popular: false },
  { id: "p5", cpu: 5, ram: 10, ramType: "DDR5", storage: 155, storageType: "NVMe", price: 29, pricePerHour: 0.07, name: "5 Cores | 10 GB DDR5 | 155 GB", popular: false },
  { id: "p6", cpu: 6, ram: 12, ramType: "DDR5", storage: 185, storageType: "NVMe", price: 35, pricePerHour: 0.09, name: "6 Cores | 12 GB DDR5 | 185 GB", popular: false },
  { id: "p7", cpu: 8, ram: 16, ramType: "DDR5", storage: 275, storageType: "NVMe", price: 49, pricePerHour: 0.11, name: "8 Cores | 16 GB DDR5 | 275 GB", popular: false },
  { id: "p8", cpu: 10, ram: 20, ramType: "DDR5", storage: 325, storageType: "NVMe", price: 60, pricePerHour: 0.13, name: "10 Cores | 20 GB DDR5 | 325 GB", popular: false },
  { id: "p12", cpu: 2, ram: 2, ramType: "DDR4", storage: 35, storageType: "NVMe", price: 4, pricePerHour: 0.02, name: "2 Cores | 2 GB DDR4 | 35 GB", popular: false },
  { id: "p13", cpu: 2, ram: 4, ramType: "DDR4", storage: 55, storageType: "NVMe", price: 6, pricePerHour: 0.04, name: "2 Cores | 4 GB DDR4 | 55 GB", popular: false },
  { id: "p14", cpu: 3, ram: 4, ramType: "DDR4", storage: 65, storageType: "NVMe", price: 8, pricePerHour: 0.05, name: "3 Cores | 4 GB DDR4 | 65 GB", popular: false },
  { id: "p15", cpu: 4, ram: 6, ramType: "DDR4", storage: 85, storageType: "NVMe", price: 11, pricePerHour: 0.06, name: "4 Cores | 6 GB DDR4 | 85 GB", popular: false },
]

export default function BuyingPage() {
  const [plans] = useState(allPlans)
  const [selectedPlan, setSelectedPlan] = useState("")
  const [serverName, setServerName] = useState("")
  const [billingPeriod, setBillingPeriod] = useState("monthly")
  const [loading, setLoading] = useState(false)
  const [userBalance] = useState(50)

  useEffect(() => {
    if (!selectedPlan && plans.length > 0) setSelectedPlan(plans[0].id)
  }, [plans, selectedPlan])

  const calculatePrice = (base) =>
    billingPeriod === "monthly" ? base :
      billingPeriod === "quarterly" ? base * 3 * 0.95 : base * 12 * 0.9

  const selectedPlanData = plans.find(p => p.id === selectedPlan)
  const totalPrice = selectedPlanData ? calculatePrice(selectedPlanData.price) : 0
  const hasInsufficientBalance = userBalance < totalPrice

  const handleCreate = async () => {
    if (!selectedPlan || !serverName.trim()) {
      alert("Please fill all fields")
      return
    }
    setLoading(true)
    setTimeout(() => {
      alert("Server created successfully!")
      setServerName("")
      setLoading(false)
    }, 1500)
  }

  const emojiComponents = [
    <FaSmile className="text-yellow-500 w-6 h-6 sm:w-8 sm:h-8 mx-auto" key="smile" />,
    <FaLaughBeam className="text-orange-500 w-6 h-6 sm:w-8 sm:h-8 mx-auto" key="laugh" />,
    <FaGrinStars className="text-blue-500 w-6 h-6 sm:w-8 sm:h-8 mx-auto" key="grin" />
  ]

  const t = {
    "servers.createServer": "Create New Server",
    "servers.serverName": "Server Name",
    "servers.billingPeriod": "Billing Period",
    "servers.monthly": "Monthly",
    "servers.quarterly": "Quarterly",
    "servers.yearly": "Yearly",
    "servers.selectPlan": "Select Plan",
    "servers.insufficientBalance": "Insufficient Balance",
    "servers.currentBalance": "Current Balance",
    "servers.selectedPlan": "Selected Plan",
    "servers.total": "Total",
    "servers.backToServers": "← Back to Servers"
  }

  return (
    <div className="w-full min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 dark:from-slate-900 dark:to-slate-800">
      <div className="max-w-7xl mx-auto py-6 sm:py-8 md:py-12 px-3 sm:px-4 md:px-6 lg:px-8">
        {/* Header */}
        <div className="text-center mb-6 sm:mb-8 md:mb-12">
          <div className="inline-flex items-center justify-center mb-3 sm:mb-4">
            {emojiComponents[2]}
          </div>
          <h1 className="text-2xl sm:text-3xl md:text-4xl lg:text-5xl font-extrabold bg-clip-text text-transparent bg-gradient-to-r from-blue-600 to-purple-600 dark:from-blue-400 dark:to-purple-400">
            {t["servers.createServer"]}
          </h1>
        </div>

        <div className="space-y-6 sm:space-y-8">
          {/* Server Name */}
          <div className="bg-white dark:bg-slate-800 rounded-2xl sm:rounded-3xl p-4 sm:p-6 md:p-8 shadow-lg border border-slate-200 dark:border-slate-700">
            <Label htmlFor="serverName" className="text-base sm:text-lg font-semibold mb-2 block">
              {t["servers.serverName"]}
            </Label>
            <Input
              id="serverName"
              placeholder="My VPS Server"
              value={serverName}
              onChange={(e) => setServerName(e.target.value)}
              className="h-12 sm:h-14 text-base sm:text-lg rounded-xl"
            />
          </div>

          {/* Billing Period */}
          <div className="bg-white dark:bg-slate-800 rounded-2xl sm:rounded-3xl p-4 sm:p-6 md:p-8 shadow-lg border border-slate-200 dark:border-slate-700">
            <Label className="text-base sm:text-lg font-semibold mb-3 sm:mb-4 block">
              {t["servers.billingPeriod"]}
            </Label>
            <div className="grid grid-cols-1 sm:grid-cols-3 gap-3 sm:gap-4">
              {["monthly", "quarterly", "yearly"].map((period, idx) => (
                <Button
                  key={period}
                  type="button"
                  variant={billingPeriod === period ? "default" : "outline"}
                  onClick={() => setBillingPeriod(period)}
                  className={`h-16 sm:h-20 md:h-24 text-base sm:text-lg font-semibold rounded-xl flex flex-col justify-center items-center gap-1 sm:gap-2 transition-all ${
                    billingPeriod === period ? "shadow-lg scale-[1.02] ring-2 ring-primary ring-offset-2" : "hover:shadow-md hover:scale-[1.01]"
                  }`}
                >
                  <div className="scale-75 sm:scale-100">
                    {emojiComponents[idx % 3]}
                  </div>
                  <span className="text-sm sm:text-base md:text-lg">{t[`servers.${period}`]}</span>
                  {period === "quarterly" && <span className="text-xs sm:text-sm opacity-80">-5%</span>}
                  {period === "yearly" && <span className="text-xs sm:text-sm opacity-80">-10%</span>}
                </Button>
              ))}
            </div>
          </div>

          {/* Plans */}
          <div className="bg-white dark:bg-slate-800 rounded-2xl sm:rounded-3xl p-4 sm:p-6 md:p-8 shadow-lg border border-slate-200 dark:border-slate-700">
            <Label className="text-base sm:text-lg font-semibold mb-3 sm:mb-4 block">
              {t["servers.selectPlan"]}
            </Label>
            
            {/* Mobile: Vertical Stack */}
            <div className="block sm:hidden space-y-3">
              {plans.map((plan, idx) => (
                <div
                  key={plan.id}
                  onClick={() => setSelectedPlan(plan.id)}
                  className={`relative cursor-pointer rounded-xl border-2 p-4 transition-all ${
                    selectedPlan === plan.id 
                      ? "border-primary bg-primary/5 shadow-md ring-2 ring-primary ring-offset-2" 
                      : "border-slate-200 dark:border-slate-700 hover:border-primary/50 hover:shadow-md"
                  }`}
                >
                  {plan.popular && (
                    <Badge className="absolute -top-2 -right-2 bg-gradient-to-r from-primary to-blue-500 text-white text-xs px-2 py-1 rounded-full">
                      POPULAR
                    </Badge>
                  )}
                  <div className="flex items-center gap-4">
                    <div className="flex-shrink-0">
                      {emojiComponents[idx % emojiComponents.length]}
                    </div>
                    <div className="flex-1 min-w-0">
                      <h3 className="font-bold text-sm mb-1 truncate">{plan.name}</h3>
                      <div className="flex items-baseline gap-2">
                        <span className="text-lg font-extrabold text-primary">${plan.price}</span>
                        <span className="text-xs text-muted-foreground">/mo</span>
                      </div>
                    </div>
                    {selectedPlan === plan.id && (
                      <div className="flex-shrink-0 w-8 h-8 bg-primary text-primary-foreground rounded-full flex items-center justify-center">
                        <FaGrinStars className="w-5 h-5" />
                      </div>
                    )}
                  </div>
                  <div className="mt-3 pt-3 border-t border-slate-200 dark:border-slate-700 grid grid-cols-3 gap-2 text-xs text-center">
                    <div>
                      <div className="font-bold text-sm">{plan.cpu}</div>
                      <div className="text-muted-foreground">Cores</div>
                    </div>
                    <div>
                      <div className="font-bold text-sm">{plan.ram} GB</div>
                      <div className="text-muted-foreground">{plan.ramType}</div>
                    </div>
                    <div>
                      <div className="font-bold text-sm">{plan.storage} GB</div>
                      <div className="text-muted-foreground">{plan.storageType}</div>
                    </div>
                  </div>
                </div>
              ))}
            </div>

            {/* Tablet & Desktop: Horizontal Scroll */}
            <div className="hidden sm:block overflow-x-auto py-4 -mx-2 px-2">
              <div className="flex gap-4 md:gap-6 min-w-max pb-2">
                {plans.map((plan, idx) => (
                  <div
                    key={plan.id}
                    onClick={() => setSelectedPlan(plan.id)}
                    className={`relative cursor-pointer rounded-2xl border-2 p-4 md:p-6 transition-all hover:shadow-xl hover:scale-[1.02] w-48 md:w-56 flex-shrink-0 ${
                      selectedPlan === plan.id 
                        ? "border-primary bg-primary/5 shadow-lg ring-2 ring-primary ring-offset-2" 
                        : "border-slate-200 dark:border-slate-700 hover:border-primary/50"
                    }`}
                  >
                    {plan.popular && (
                      <Badge className="absolute -top-3 right-3 bg-gradient-to-r from-primary to-blue-500 text-white text-xs px-3 py-1 rounded-full shadow-md">
                        POPULAR
                      </Badge>
                    )}
                    {selectedPlan === plan.id && (
                      <div className="absolute -top-3 -right-3 w-8 h-8 md:w-10 md:h-10 bg-primary text-primary-foreground rounded-full flex items-center justify-center shadow-lg">
                        <FaGrinStars className="w-5 h-5 md:w-6 md:h-6" />
                      </div>
                    )}
                    <div className="mb-3 flex justify-center">
                      {emojiComponents[idx % emojiComponents.length]}
                    </div>
                    <h3 className="font-bold text-sm md:text-base text-center mb-2 min-h-[2.5rem] flex items-center justify-center">
                      {plan.name}
                    </h3>
                    <div className="text-center mb-3 md:mb-4">
                      <div className="text-xl md:text-2xl font-extrabold text-primary">${plan.price}/mo</div>
                      <div className="text-xs md:text-sm text-muted-foreground">${plan.pricePerHour}/hr</div>
                    </div>
                    <div className="space-y-1 text-xs md:text-sm text-center">
                      <div><span className="font-bold">{plan.cpu}</span> Cores</div>
                      <div><span className="font-bold">{plan.ram}</span> GB {plan.ramType}</div>
                      <div><span className="font-bold">{plan.storage}</span> GB {plan.storageType}</div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* Insufficient Balance Alert */}
          {hasInsufficientBalance && selectedPlanData && (
            <Alert variant="destructive" className="rounded-2xl border-2">
              <div className="flex items-start gap-3 sm:gap-4">
                <FaSadTear className="h-5 w-5 sm:h-6 sm:w-6 md:h-7 md:w-7 text-red-600 flex-shrink-0 mt-0.5" />
                <AlertDescription className="text-sm sm:text-base md:text-lg">
                  {t["servers.insufficientBalance"]} — {t["servers.currentBalance"]}: ${userBalance.toFixed(2)}
                </AlertDescription>
              </div>
            </Alert>
          )}

          {/* Summary */}
          {selectedPlanData && (
            <div className="bg-gradient-to-br from-slate-50 to-slate-100 dark:from-slate-800 dark:to-slate-700 border-2 border-slate-200 dark:border-slate-600 rounded-2xl sm:rounded-3xl p-4 sm:p-6 md:p-8 shadow-inner">
              <div className="space-y-3 sm:space-y-4">
                <div className="flex flex-col sm:flex-row sm:justify-between gap-1 sm:gap-0">
                  <span className="text-sm sm:text-base text-muted-foreground">{t["servers.selectedPlan"]}:</span>
                  <span className="font-semibold text-sm sm:text-base break-words">{selectedPlanData.name}</span>
                </div>
                <div className="flex flex-col sm:flex-row sm:justify-between gap-1 sm:gap-0">
                  <span className="text-sm sm:text-base text-muted-foreground">{t["servers.billingPeriod"]}:</span>
                  <span className="font-semibold text-sm sm:text-base">{t[`servers.${billingPeriod}`]}</span>
                </div>
                <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center gap-2 pt-3 border-t-2 border-slate-300 dark:border-slate-600">
                  <span className="text-lg sm:text-xl md:text-2xl font-semibold">{t["servers.total"]}:</span>
                  <span className="text-primary text-2xl sm:text-3xl md:text-4xl font-extrabold">${totalPrice.toFixed(2)}</span>
                </div>
              </div>
            </div>
          )}

          {/* Action Buttons */}
          <div className="flex flex-col sm:flex-row gap-3 sm:gap-4 pt-4">
            <Button
              variant="outline"
              onClick={() => window.history.back()}
              className="h-12 sm:h-14 text-base sm:text-lg font-semibold rounded-xl w-full sm:w-auto sm:flex-1 order-2 sm:order-1"
            >
              {t["servers.backToServers"]}
            </Button>
            <Button
              onClick={handleCreate}
              disabled={loading || hasInsufficientBalance || !serverName.trim()}
              className="h-12 sm:h-14 text-base sm:text-lg font-semibold rounded-xl w-full sm:flex-[2] shadow-lg hover:shadow-xl transition-all order-1 sm:order-2"
            >
              {loading ? "Creating..." : "Create Server"}
            </Button>
          </div>
        </div>
      </div>
    </div>
  )
}